// SECTION INTRO

#intro{
     background: #FFF8F8;
}

.intro-container{
    padding: 60px 0px;
}
.intro-block{
    border-right: 1px solid#eee;
}

.no-border{
    border: 0px;
}
.intro-icon img {
    max-width: 45px;
}
.intro-icon{
    width: 80px;
    height: 80px;
    text-align: center;
    border-radius: 50px 3px 50px 50px;
    padding-top: 15px;
    background: $primary-trans;
    margin-bottom: 25px;
}
.intro-content {
    h4{
        margin-bottom: 15px;
        font-size: 20px;
    }
    p{
        margin-bottom: 0px;
    }
}


// ABOUT SECTION


.about-img-block {
    img{
        border-radius: 5px;
    }
}

.about-content{
    position: relative;
    i{
        position: absolute;
        left: 0px;
        font-size: 30px;
        margin-left: 20px;
        color: $primary-color;
        display: none;
    }
    ul{
        margin-top: 60px;
    }
    ul li{
        padding-left: 60px;
        border: 1px solid $border-color;
        padding: 30px 0px 0px 30px;
        margin-bottom: 20px;
        border-radius: 5px;
        width: 42%;
        float: left;
        margin-right: 60px;
    }
}


.about-block{
    span{
        font-size: 120px;
        float: left;
        font-weight: 700;
        margin-right: 15px;
    }
    
    h2{
        padding-top: 40px;
    }
    
    p{
        float: none;
        clear: both;
        margin-top: 10px;
    }
}


//SECTION SERVICES


#section-services{
    background: url("/images/bg/6.jpg") no-repeat fixed 0% 0%;
    background-size: cover;
    position: relative;
    padding-top: 0px;
    margin-top: 140px;
    padding-bottom: 0px;
    
    
    &:before{
        position: absolute;
        content:"";
        left: 0px;
        top: 0px;
        width: 100%;
        height: 100%;
        background: rgba(238, 45, 80,.6);
    }
}

.service-wrap{
    position: relative;
    top: -115px;
}
.service-block{
    background: $white;
    padding: 45px 30px 75px 30px;
    text-align: center;
    margin-bottom: 30px;
    border-radius: 5px;
    box-shadow: 0 10px 40px rgba(0,0,0,.05);
    border: 1px solid#eee;
    transition: all .7s ease;
    position: relative;
    
    h4{
        margin-bottom: 20px;
    }
    
    p{
        margin-bottom: 0px;
    }
    
    i{
        font-size: 100px;
        color: $primary-color;
        display: inline-block;
        background: transparent;
        padding: 20px;
        width: 200px;
        height: 200px;
        border-radius: 100%;
        padding-top: 10px;
        transition: all .7s ease;
    }
    &:hover{
        border-color: $secondary-color;
    }
    &:hover i {
        background: $primary-trans;
        margin-bottom: 25px;
    }
    
}




.service-btm{
    font-size: 22px;
    color: $white;
    margin-top: 30px;
    p{
        margin-bottom: 0px;
    }
    
    a{
        background: $white;
        padding: 10px 40px;
        border-radius: 5px;
        font-size: 22px;
    }
}


//SECTION PROCESS

#section-process{
    position: relative;
    padding-bottom: 40px;
}
.process-img-block{
    img{
        border-radius: 5px;
    }
    
    .process-content{
        font-weight: 400;
        margin-top: 25px;
        
        span{
            font-weight: 700;
            width: 40px;
            height: 40px;
            text-align: center;
            background: $primary-trans;  
            float: left;
            color: $black;
            padding-top: 5px;
            margin-right: 25px;
            border-radius: 20px 20px 0px 20px;
        }
        p{
            font-size: 18px;
            color: $secondary-color;
        }
    }
    
}


// PORTFOLIO SECTION


.slick-slide {
    width: 450px;

}
.item-box{
    box-shadow: 5px 5px 10px  rgba(130, 136, 147, 0.15);
    margin: 15px 15px 15px 15px;
    position: relative;
    transition: all .4s ease;
    border: 1px solid#eee;
    border-radius: 5px;
    max-height:550px;
    height:auto;
    
    img{
        border-radius: 5px;
    }
    
    .item-overlay-text{
        position: absolute;
        bottom: 40px;
        padding: 14px 30px;
        background: $secondary-trans;
        margin: 0px 20px;
        border-radius: 5px;
        opacity: 0;
         transition: all .4s ease;

         h5{
             margin-bottom: 0px;
             color:$black;
         }
    }
    
    &:hover .item-overlay-text{
        opacity: 1;
    }
}


.slick-dots{
    bottom: -50px;
    
    li {
        margin: 0 3px;
        padding: 0;
        cursor: pointer;
    }
    
   li button::before {
        font-size: 13px;
    }
}

.slick-active{
    color: $black;
}

// COUNETR SECTION


#section-statics{
    background: #FAFBFD;
    position: relative;
    padding-top: 370px;
    
    &:before{
        position: absolute;
        content:"";
        left: 0px;
        top: 0px;
        background: url("/images/bg/cst-bg1.jpg") no-repeat;
        width: 100%;
        height: 100%;
    }
}

.stat-content{
    padding-right: 20px;
    p{
        font-size: 18px;
        margin-top: 20px;
    }
    
    ul li{
        font-size: 16px;
        margin: 10px 0px;
        
        i{
            margin-right: 15px;
            color: $primary-color;
        }
    }
}

.counter-stat{
    padding:55px 20px;
    text-align: center;
    margin: 65px 0px 30px 0px;
    border:1px solid rgba(0,0,0,0.03);
    
    .counter{
        font-size: 59px;
        font-family: $base-font;
    }
    
    p{
        text-transform: uppercase;
        letter-spacing: 2px;
        margin-bottom: 0px;
    }
    
    
}

.border-stat > div {
    padding: 0;
    border: 1px solid #e1e1e1;
}



// TESTIMONIAL SECTION


#section-testimonial{
    .section-heading {
        margin-bottom: 50px;
    }
}
.test-content{
    text-align: center;
    

    p.lead{
        font-weight: 400;
    }
    .test-img{
        width: 70px;
        height: 70px;
        background: $primary-trans;
        border-radius: 100%;
        margin:60px auto 30px;
            
        img{
            max-width: 90px;
        }
    }
}


// SECTION PRICING



#section-pricing{
    position: relative;
    
    .section-heading{
        position: relative;
        z-index: 3;
        
        h5{
            color: $white;
        }
        h2{
            color: $white;
        }
    }
}
#section-pricing:after{
    position: absolute;
    content:"";
    left: 0px;
    top: 0px;
    background: url("/images/bg/5.jpg")no-repeat fixed 50% 50%;
    background-size: cover;
    width: 100%;
    height: 560px;
}
#section-pricing:before{
    position: absolute;
    content:"";
    left: 0px;
    top: 0px;
    background: rgba(40, 58, 94,.6);
    background-size: cover;
    width: 100%;
    height: 560px;
    z-index: 1;
}

.pricing-table{
    text-align: center;
    box-shadow: 0 10px 40px rgba(0,0,0,.05);
    padding: 40px 0px 70px 0px;
    border-radius: 5px;
    border-top: 3px solid transparent; 
    transition: all .7s ease;
    background: #fff;
    z-index: 2;
    position: relative;
    
    .pricing-header{
        margin-bottom: 30px;
        h6{
            text-transform: capitalize;
            font-size: 13px;
            font-weight: 400;
        }
        
        h2{
            font-size: 80px;
            margin-top: 15px;
            letter-spacing: -3px;
            position: relative;
            
            small{
                font-size: 24px;
                color: $gray-light;
                font-size: 24px;
                position: absolute;
                top: 13px;
                margin-left: 9px;
            }
        }
        
        h4{
            font-size: 30px;
            font-weight: 400;
            text-transform: capitalize;
        }
    }
    
    .pricing-feature{
        margin: 40px 0px 50px;
        
        li{
            margin: 10px 0px;
            color: #828893;
        }
    }
    
    
    &:hover{
        border-top: 3px solid $primary-color; 
    }
    
    &:hover a.btn-outline-hero{
        background: $primary-color;
        color: $white;
        border-color: $primary-color;
    }
    
}


.pricing-table.active{
    border-top: 3px solid $primary-color; 
}

.pricing-feature.basic li:nth-child(2n){
        text-decoration: line-through;
    }


// BLOG SECTION


#section-blog{
    position: relative;
    
    &:before{
        position: absolute;
        content:"";
        right: 0px;
        top: 50px;
        width:38%;
        height: 100%;
        background: url("/images/bg/p1.png")no-repeat;
    }
}


.single-blog{
    
    img{
        border-radius: 5px;
    }
    
    .learn-more{
        color: $primary-color;
    }
}


.blog-meta{
    margin-top: 30px;
    margin-bottom: 15px;
    
    span{
        font-size: 16px;
    }
    
    .post-date{
        position: relative;
        
        i{
            margin-right: 10px;
            color: $secondary-color;
        }
        &:before{
            content: "/";
            display: inline-block;
            position: absolute;
            top: -2px;
            right: -15px;
        }
    }
    
    .post-cat{
        margin-left: 20px;
    }
}
.blog-text{
    margin-top: 10px;
    a{
        margin-bottom: 20px;
        display: block;
    }
}

.share-links{
    margin-top: 50px;
}



// SECTION CTA

#section-cta{
    background: #fafbfd;
    position: relative;
    
    &:before{
        position: absolute;
        content:"";
        right: 0px;
        top: 0px;
        width: 46%;
        height: 100%;
        background: url("/images/bg/cta-bg.jpg")no-repeat;
        background-size: cover;
    }
}



.cta-content{
    .form-control{
        height: 70px;
        margin-bottom: 30px;
    }
}














