.btn {
  line-height: 2rem;
    border: 2px solid $border-color;
    padding: 10px 35px;
    border-radius: 50px;
    text-transform: capitalize;
    transition: all .7s ease;
}

.btn-hero{
  background: $primary-color;
  color: $white;
  border-color: $primary-color;
  &:hover{
    background: $black;
      border-color:$black;
      color:$white;
  }
}

.btn-outline-hero{
  
    &:hover{
        border-color: $primary-color;
    }
}

.btn-hero-trans {
  background: transparent;
  color: $black;
    border-color:$primary-color;
  &:hover{
     background: $primary-color;
      border-color: $primary-color;
      color: $white;
  }
}


.btn-secondary {
  background: $secondary-color;
  border-color: $secondary-color;
  color: $white;
  &:hover{
    background: $black;
    color: $white;
  }
}
.btn-secondary-trans {
  background: $secondary-trans;
  border-color: $secondary-trans;
  color: $black;
  &:hover{
    background: $secondary-color;
   border-color: $secondary-color;
      color: $white;
  }
}