#cta{
	padding: 120px 0px;
	background: url("../images/bg/5.jpg") no-repeat 0% 30%;
	background-size: cover;

	.cta-block{
		i{
			font-size: 65px;
		    margin-right: 30px;
		}

		h2{
			font-size: 75px;
			line-height: 75px;
		}
	}

}

.video{
	a{
		font-size: 40px;
		line-height: 70px;
		display: inline-block;
		border: 1px solid $white;
		width: 150px;
		height: 150px;
		text-align: center;
		border-radius: 100%;
		background:rgba(255,255,255,.9);
		padding-top: 40px;
		color: $primary-color;
	}
}

