

@import url('https://fonts.googleapis.com/css?family=Play:400,700|Sarabun:400,600,700');
body{
  font-family: $base-font;
  font-size: $base-font-size;
  font-weight: 400;
  line-height: 1.8;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -ms-text-size-adjust: 100%;
  color: $black-lighter;
  overflow-x: hidden;
}

body, html {
  height: 100%;
}

a {
  color: $black;
  transition: .3s all;
  &:hover{
    color: $primary-color;
  }
}

a,
a:hover,
button,
button:focus {
  transition: all .3s;
  text-decoration: none;
  outline: 0;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
  font-weight:700;
  color: $secondary-color;
  font-family: $heading-font;
}


h1, .h1 {
  font-size: 48px;
}

h2, .h2 {
  font-size: 36px;
}


h3, .h3 {
  font-size: 26px;
}

h4, .h4 {
  font-size: 22px;
    line-height: 30px;
}


h5, .h5 {
  font-size: 18px;
}

h6, .h6 {
  font-size: 16px;
}

p{
  line-height: 28px;
  letter-spacing: .3px;
}


.form-control {
  border-color: $border-color;
}

.lead{
  line-height: 40px;
}