.page-banner{
    .banner-padding{
        padding: 225px 0px 110px;
        
        h6{
            text-transform: uppercase;
            font-weight: 400;
            letter-spacing: 2px;
        }
        
        h1{
            margin: 10px 0px 10px;
        }
        
        ul li{
            font-size: 16px;
            text-transform: capitalize;
        }
        
        ul li:last-child{
            position: relative;
            padding-left: 20px;
            color: $primary-color;
            
            &:before{
            content: "/";
            display: inline-block;
            position: absolute;
            top: 0px;
            left: 0px;
            }
        }
    }
}


.page-image{
    background: #eee;
    position: relative;
    background: url("/images/banner/banner-4.jpg") no-repeat 50% 50%;
    background-size: cover;
    height: 400px;
}

.page-service-image{
    background: #eee;
    position: relative;
    background: url("/images/banner/6.jpg") no-repeat 50% 50%;
    background-size: cover;
    height: 400px;
}

.page-blog-image{
    background: #eee;
    position: relative;
    background: url("/images/banner/blog-2.jpg") no-repeat 50% 50%;
    background-size: cover;
    height: 400px;
}


.page-contact-image{
    background: #eee;
    position: relative;
    background: url("/images/banner/cta-bg.jpg") no-repeat 50% 50%;
    background-size: cover;
    height: 400px;
}

.mb50{
    margin-bottom: 65px;
}



.pagination-links {
    border: 1px solid $border-color;
    margin-bottom: 60px;
    padding: 10px 30px;
    
    ul{
        margin-bottom: 0px;
    }
    li{
        padding: 7px;
        font-size: 16px;
        color: $black;
        font-weight: 700;
        
        i{
            margin: 0px 5px;
        }
    }
    
}

.pagination-links li:last-child a,.pagination-links li:first-child a{
    background: transparent;
}






// SINGLE BLOG PAGE


#blog-single{
    padding: 40px 0px;
}

.large-blog{
    
    .blog-text{
        margin-top: 0px;
    }
    
    p.lead{
        color: $black;
        font-weight: 400;
    }
    .single-blog-meta{
        
        margin-bottom: 25px;
        
        span{
            font-size: 16px;
            margin-right: 20px;
            
            i{
                color: $white;
                margin-right: 10px;
                width: 30px;
                height: 30px;
                background: $secondary-color;
                text-align: center;
                line-height: 27px;
                border-radius: 2px;
            }
        }
    }
}



blockquote.blog-quote{
    color: $black;
    font-size: 20px;
    padding: 30px;
    line-height: 40px;
    margin-bottom: 40px;
    border-left: 2px solid $primary-color;
}
.comment-box{
    margin-top: 100px;

    .comment-form{
        margin-top: 30px;
    }
    .form-group{
        margin-bottom: 1.2rem;
    }
    
    h4{
        margin-bottom: 10px;
    }
}



#nav-pagination{
    margin-bottom: 30px;
    margin-top: 50px;
    border-top: 1px solid $border-color;
    border-bottom: 1px solid $border-color;
    padding: 25px 0px;

    .pagination-prev ,.pagination-next{
        a{
            font-size: 16px;
            font-weight: 700;
            text-transform: uppercase;
        }
    }
    
    .pagination-prev{
        a i{
            margin-right: 20px;
        }
    }
    
    .pagination-next  {
        float: right;
        a i{
            margin-left: 20px;
        }
    }

}



/// SIDEBAR BLOG

.sidebar-block {
    margin-bottom: 30px;
}


 .search-form{
        position: relative;
        .btn{
            position: absolute;
            right: 2px;
            top: 2px;
            border-radius: 5px;
        }
    }


.category{
    border: 1px solid $border-color;
    padding: 25px 20px;
    h5{
        margin-bottom: 20px;
    }
    
    a{
        margin: 5px 0px;
        border-bottom: 1px solid $border-color;
        display: inline-block;
        color: $black;
    }
}


.tags{
    a{
        padding: 3px 7px;
        margin: 0px 3px 5px 0px;
        background: $primary-trans;
        border-radius: 3px;
        display: inline-block;
    }
}


.download{
    a{
        display: block;
        border: 1px solid $border-color;
        padding: 10px 20px;
        margin-bottom: 10px;
        background: $gray-lighter;
        i {
            margin-right: 10px;
            color: $primary-color;
        }
    }
}



//// CONTACT PAGE


.form-control{
    height: 50px;
    transition: all .3s ease;
}


.form-control:focus, .form-control:hover{
    box-shadow: none;
    border-color: $primary-color;
}

.contact-info-block{
    margin-bottom: 50px;
    h5{
        margin-bottom: 20px;
        color: $primary-color;
    }
    
    ul li{
        margin: 10px 0px 15px 0px;
        color: #777;
        font-size: 16px;
        text-transform: capitalize;
        
        i{
            margin-right: 15px;
            font-size: 16px;
            color: $secondary-color;
            width: 40px;
            height: 40px;
            border: 1px solid $border-color;
            text-align: center;
            padding-top: 10px;
            border-radius: 100%
        }
    }
}


.contact-form {
    background: $gray-lighter;
    padding: 40px 20px 15px 20px;
    
    .form-control{
        border-radius: 0px;
    }
    .form-group{
        margin-bottom: 1.2rem;
    }
    ::placeholder{
        color: $black;
        font-size: 13px;
        text-transform: uppercase;
        letter-spacing: 2px;
    }
    
}



/// ABout 2

.intro-block-2{
    text-align: center;
    padding: 50px 30px 35px;
    border: 1px solid#eee;
    .intro-icon {
        border-radius: 5px;
        margin: 0 auto 35px;
    }
}