#process-2{
	position: relative;

	&:before{
		position: absolute;
		content:"";
		left: 0px;
		top:0px;
		width: 41%;
		height:100%;
		background: url("../images/bg/cta-bg.jpg");
		background-size: cover;
	}

}


.about{
	position: relative;

	&:before{
		position: absolute;
		content:"";
		right: 0px;
		top:0px;
		width: 30%;
		height:100%;
		background: rgba(238, 45, 80, 0.05);
	}

}

#service{
	@extend .about;
}