.section{
    padding: 90px 0px;
}

.section-top{
    padding-top: 90px;
}

.section-bottom{
    padding-bottom: 90px;
}

.mb-30{
    margin-bottom: 30px;
}

.section-heading{
    margin-bottom: 75px;
}

.read-more{
    color: $primary-color;
    transition: all .7s ease;
    text-transform: uppercase;
    letter-spacing: .5px;
    
    i{
        padding-left: 10px;
        transition: all .7s ease;
    }
    
    &:hover{
        color: $secondary-color;
    }
    &:hover i{
        padding-left: 20px;
    }
}

.letter-spacing{
    letter-spacing: 1px;
}

.text-color{
    color: $primary-color;
}