
.banner-padding{
    padding: 240px 0px 160px 0px;
}



.main-banner-content{
    h1{
        font-size: 45px;
        color: $secondary-color;
    }
    p{
        font-size: 20px;
        margin: 22px 0px 45px;
        padding-right: 30px;
        line-height: 38px;
        color: #343434;
    }
    .subtitle{
        color: #343434;
    }
}



.btn-container{
    margin-top: 20px;
    a {
        margin-right: 10px;
    }
}


.subtitle{
    text-transform: uppercase;
    font-size: 13px;
    color: #2c2c2c;
    font-weight: 400;
    letter-spacing: 6px;
    position: relative;
    padding-top: 10px;
}

.subtitle:before{
    position: absolute;
    content:"";
    top: 0px;
    width: 30px;
    height: 1px;
    background: #ee2d50;
}


// Banner -2 Styles /* hero slider */

.hero-slider{
    h1{
        font-size: 70px;
        line-height: 80px;
        color: $white;
    }

    .btn-outline-hero{
        color: $white;
    }
}

.overlay{
    position: absolute;
    content:"";
    left: 0px;
    top:0px;
    width: 100%;
    height:100%;
    background:rgba(40, 58, 94,.9);
}

.bg-cover {
    background-size: cover!important;
    background-position: center center!important;
    background-repeat: no-repeat!important;
}

.slick-slide {
    height: 100%;
}



.hero-section {
  padding:70px 0px;

  @exclude .overlay;
}


.hero-slider{
    .slick-dots {
        bottom: 40px;
    }

    .slick-dots li button::before {
        font-size: 8px;

    }

    .slick-dots li button::before{
        color: $white;
         opacity: .6;
    }
    .slick-dots li.slick-active button::before{
        color: $primary-color;
        opacity: 1;
    }

}